
/* styles */
/* called by your view template */

* {
  box-sizing: border-box;
  outline: none;

}

body {
  font-family: "Benton Sans", "Helvetica Neue", helvetica, arial, sans-serif;
  margin: 2em;
}

.appContainer{
  margin-top: 20px;
  padding:20px;
  overflow: auto;
}

.column{
  border-right: 1px dotted #ddd;
}

.loading{
    background: black;
}

.navbar{
    background: #f4f4f4;
}
.houses{
    height: 88vh;
    overflow: scroll;    
    padding:20px;
}

.house:hover{
      background-color: #1DB;
}
.funds{
    padding: 15px;
    height: 88vh;
    overflow: scroll;
}

.fund:hover{
    background-color: #ffe;
}

.tag{
  overflow-wrap: break-word;
  word-break: break-all;
  height: auto !important;
  white-space: unset !important;  

}


.NAV{
  width: 100%;
  font-size: 1.8em;
  margin-bottom: 10px;
  color: green;
}

.quandl.box{
  padding: 15px;
  margin-top:20px;
}

.quandl>span{
  margin-left: 25px;
}
.container{
  overflow-wrap: break-word;
  width:100%;
  word-break: break-all;
  height: auto !important;
}

.container>.title{
  margin-bottom:40px;
}

h1
{
  font-size: 2em;
}

.dview>.box{
  margin-top: 20px;
}

.subtitle.box{
    color: black;
}

.hero{
/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#aaffdd+0,fff38c+100,eaf986+100&1+0,0.68+100 */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to bottom, rgba(170,255,221,1) 0%,rgba(234,249,134,0.68) 150%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#aaffdd', endColorstr='#adeaf986',GradientType=0 ); /* IE6-9 */
border-radius: 5px;
}


.detail{
    margin-top: -25px;
}
form{
  display: -webkit-flex;
  display: flex;
  margin-bottom: 20px;
}

form>input{
    font-size: 1em;
    width: 100%;
    padding: 10px;
    border-radius: 0px 40px 40px 0px;
    background: #f6f6f6;
    border: 0px;
    color:#999;
}

.SearchCapsule{
    margin-top: 60px; 
    border-radius: 40px;
    /*  border: 2px solid #DDD; */
}

.SCDropDown{
    font-size: 1em;
    margin-top: 5px;
    background: transparent;
    border: 0px;
}

.SCDropDown>option{

}

.DropDownStyle{
    background: #afd;
    padding: 10px;
    border-radius: 40px 0px 0px 40px;
    border: 1px solid #EEE;
}
